/***************************************************************************************************
 * BROWSER POLYFILLS
 */

// Import core-js polyfills for ES features
import 'core-js/es/array';    // Polyfills for Array methods like includes, find, flat-map, etc.
import 'core-js/es/object';   // Polyfills for Object methods (entries, assign, values)
import 'core-js/es/promise';  // Polyfill for Promise support
import 'core-js/es/symbol';   // Polyfill for Symbol
import 'core-js/es/map';      // Polyfill for Map support
import 'core-js/es/set';      // Polyfill for Set support
import 'core-js/es/string';   // Polyfills for String methods (includes, pad-start)
import 'core-js/features/reflect'; // Polyfill for Reflect API

// Web Animations for Angular Material (if you use animations)
import 'web-animations-js';

// Zone.js is required by Angular
import 'zone.js';

/***************************************************************************************************
 * Additional Polyfills for async/await and older browser support
 */
import 'regenerator-runtime/runtime'; // For async/await support

if (!Array.prototype.includes) {
    Array.prototype.includes = function (this: any, searchElement: any, fromIndex: number = 0): boolean {
      if (this == null) {
        throw new TypeError('Array.prototype.includes called on null or undefined');
      }
      const O = Object(this);
      const len = O.length >>> 0;
      if (len === 0) {
        return false;
      }
      const n = fromIndex | 0;
      let k = Math.max(n >= 0 ? n : len - Math.abs(n), 0);
      while (k < len) {
        if (O[k] === searchElement) {
          return true;
        }
        k++;
      }
      return false;
    };
  }  
  